import PropTypes from "prop-types";
import Description from "./descriptionProfile/Description";
import Followers from "./statsProfile/Followers";
import Views from "./statsProfile/Views";
import Likes from "./statsProfile/Likes";

function Profile({ data }) {
  const { avatar, name, tag, location, stats } = data;

  return (
    <div className="profile">
      {
        <Description
          userImg={avatar}
          userName={name}
          userTag={tag}
          userLocation={location}
        />
      }
      <ul className="profile_items">
        {<Followers quantityFollowers={stats.followers} />}
        {<Views quantityViews={stats.views} />}
        {<Likes quantityLikes={stats.likes} />}
      </ul>
    </div>
  );
}

Profile.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Profile;